.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  position: relative;
  background: #181a3a;
  border: 1px solid #6a7bd6;
  padding: 20px;
  border-radius: 10px;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px; 
  background-color: rgb(0, 0, 0, 0);
  color: rgb(153, 153, 153);
  border: none;
  cursor: pointer;
}

.close-button:hover {
  color: #fff;
}



.button-popout {
  padding: 10px 14px;
  font-size: 18px;
  border-radius: 8px;
  background-color: #016172;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-left: 20px;
}

.title-popout {
  font-size: 30px;
  margin-bottom: 20px;
  text-align: center;
}

.button-rules{
  margin: 0 auto;
  display: block;
  padding: 20px 24px;
  font-size: 18px;
  border-radius: 8px;
  background-color: #016172;
  color: #fff;
  border: none;
  cursor: pointer;
}


@media (max-width: 768px) {
  .backdrop-rules {
    overflow: auto;
  }
  .button-rules{
    padding: 20px 24px;
    font-size: 18px;
    border-radius: 8px;
    background-color: #016172;
    color: #fff;
    border: none;
    cursor: pointer;
    right: -100px;
  }


}

